<template>
  <gs-modal
    :title="title"
    v-model="visible"
    @visible-change="visibleChange"
    @confirm="editRole"
    @cancel="$emit('close')"
  >
    <gs-form
      ref="form"
      :model="form"
      label-width="80px"
    >
      <gs-form-item
        label="角色名称"
        prop="roleName"
      >
        <span>{{ form.roleName }}</span>
      </gs-form-item>
      <gs-form-item
        label="描述"
        prop="description"
      >
        <gs-textarea v-model="form.description" />
      </gs-form-item>
    </gs-form>
  </gs-modal>
</template>
<script>
import * as service from 'src/service/auth/role';
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    role: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      visible: false,
      form: {
        roleName: '',
        description: ''
      }
    };
  },
  computed: {
    title() {
      return '编辑角色';
    }
  },
  watch: {
    show(val) {
      this.visible = val;
    }
  },
  methods: {
    editRole() {
      this.$refs.form.validate(valid => {
        if (valid) {
          service.editRole({
            roleId: this.form.roleId,
            params: {
              roleId: this.form.roleId,
              description: this.form.description
            }
          }).then(data => {
            this.$emit('close');
            this.$Message.success(`编辑成功`);
            this.$emit('refreshRoleList');
          }).catch(e => {
          });
        }
      });
    },
    visibleChange(val) {
      if (val) {
        this.$refs.form.clearValidate();
        const {roleName, description, roleId} = this.role;
        this.form = {roleName, description, roleId};
      }
    }
  }
};
</script>



